*{
  box-sizing: border-box;
  padding:0;
  margin:0;
  background-color: #d2d2d2;
}
div{
  width: 100%;
  height: 100vh;
  background: #8e448e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
} 


  
.button{
  padding: 15px 20px;
  background: #9b59b6;
  color: white;
  border: 2px solid #ecf0f1;
  outline:none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;

}